import * as actionTypes from './actionTypes';
import axios from '../../axios-orders';

export const addIngredient = (name) => {
    return {
        type: actionTypes.ADD_INGREDIENT,
        ingredientName: name,
    }
};

export const removeIngredient = (name) => {
    return {
        type: actionTypes.REMOVE_INGREDIENT,
        ingredientName: name,
    }
};

export const setIngrediants = (ingredients) => {
    return {
        type: actionTypes.SET_INGREDIENTS,
        ingredients: ingredients,
    }
}

export const fetchIngrediantsFailed = () => {
    return {
        type: actionTypes.FETCH_INGREDIENDS_FAILED,
    }
}

export const initIngredients = () => {
    return dispatch => {
        axios.get( 'https://my-react-app-faedb.firebaseio.com//ingredients.json' )
    .then( response => {
        dispatch(setIngrediants(response.data));
    } )
    .catch( error => {
        dispatch(fetchIngrediantsFailed())
    } );
    }
};


